.carItem {
  display: flex;
  width: 100%;
  width: 150px;
  flex-basis: calc((100% - 1 * 16px) / 2);
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  gap: 16px;

  @media screen and (min-width: 768px) {
    flex-basis: calc((100% - 2 * 20px) / 3);
    gap: 20px;
    width: 209px;
  }

  @media screen and (min-width: 1440px) {
    width: 274px;
    height: 426px;
    gap: 28px;
    flex-basis: calc((100% - 3 * 29px) / 4);
  }
}
.carCard {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  gap: 8px;

  @media screen and (min-width: 768px) {
    gap: 10px;
  }

  @media screen and (min-width: 1440px) {
    height: 354px;
    gap: 14px;
  }
}
.imgWrap {
  position: relative;
  display: block;
  overflow: hidden;
  width: 100%;
  height: 146px;
  object-fit: cover;
  border-radius: 14px;

  @media screen and (min-width: 768px) {
    height: 204px;
  }

  @media screen and (min-width: 1440px) {
    height: 268px;
  }
}
.img {
  top: 0;
  right: -37px;
  height: 146px;
  background: linear-gradient(
      180deg,
      rgba(18, 20, 23, 0.5) 2.5%,
      rgba(18, 20, 23, 0) 41.07%
    ),
    #f3f3f2;

  @media screen and (min-width: 768px) {
    height: 204px;
    right: -50px;
  }

  @media screen and (min-width: 1440px) {
    right: -65px;
    height: 268px;
  }
}
.heart {
  position: absolute;
  top: 14px;
  right: 14px;
  display: inline-block;
  width: 18px;
  height: 18px;
}

.heart svg {
  fill: var(--color-text-button-and-back);
  stroke: var(--color-text-button-and-back);
  transition: fill 0.2s ease-in-out, stroke 0.2s ease-in-out;
}

.favorite .heart svg {
  fill: var(--color-button);
  stroke: var(--color-button);
}
.descriptionWrap {
  display: flex;
  width: 100%;
  flex-direction: column;
  gap: 8px;
}
.titleWrap {
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  font-size: 12px;
  font-weight: 500;
  align-items: center;

  @media screen and (min-width: 768px) {
    font-size: 14px;
  }

  @media screen and (min-width: 1440px) {
    font-size: 16px;
  }
}
.title {
  font-size: inherit;
  font-weight: inherit;
}
.span {
  color: var(--color-text-loadmore);
}

.descriptListWrap {
  display: flex;
  height: 36px;
  gap: 4px;
  flex-direction: column;
}
.descriptList {
  display: inline-flex;
  height: 18px;
  gap: 6px;
  color: var(--color-text-second);
  white-space: nowrap;
  overflow: hidden;
}

.descript-item {
  display: flex;
  flex-shrink: 1;
}

.descript-item:not(:first-child):before {
  display: block;
  height: 16px;
  width: 1px;
  background-color: rgba(18, 20, 23, 0.1);
  margin-right: 6px;
  content: '';
}
.button {
  display: flex;
  width: 100%;
  height: 44px;
  padding: 12px;
  justify-content: center;
  border: none;
  border-radius: 12px;
  background: var(--color-button);
  color: var(--color-text-button-and-back);
  font-size: 14px;
  font-weight: 600;
  line-height: 1.4;
}
